













































import { onReady } from "@dcloudio/uni-app";
import { reactive, ref } from "vue";
import { useCool, useStore } from "/@/cool";

const { service, ui, router } = useCool();
const { user } = useStore();

// 倒计时
const countdown = ref<number>(60);

// 验证码长度
const len = 4;

// 保存状态
const saving = ref<boolean>(false);

// 表单
const form = reactive<any>({
	code: "1234",
	phone: router.query.phone || "",
});

// 开始倒计时
function startCountdown() {
	countdown.value = 60;

	const timer = setInterval(() => {
		countdown.value--;

		if (countdown.value < 1) {
			clearInterval(timer);
		}
	}, 1000);
}

// 重新发送短信
function reSend() {
	service.user.user
		.getCode({
			phone: form.phone,
		})
		.then(() => {
			startCountdown();
			ui.showToast("短信已发送，请查收");
		})
		.catch((err) => {
			ui.showToast(err.message);
		});
}

// 下一步
function next() {
	saving.value = true;

	service.user.user
		.loginByCode(form)
		.then(async (res) => {
			// 设置token
			user.setToken(res);

			// 设置用户信息
			await user.get();

			// 登录跳转
			router.nextLogin();
		})
		.catch((err) => {
			ui.showTips(err.message || "登录失效，请重试~");
			saving.value = false;
			form.code = "";
		});
}

onReady(() => {
	startCountdown();
});
